.modal-box {
    position: absolute;
    left: 0;
    right: 0;
    width: 50%;
    overflow:hidden;
    padding:16px;
    margin: 50px auto;
    box-sizing:border-box;
    z-index:1;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.04);
    background: white;
}

.modal-bg {
    width: 100vw;
    height: 100vh;
    z-index: 0;
    background: rgba(0, 0, 0, 0.5);
